.App {
  text-align: center;
  font-size: 1.5rem;
  padding: 1rem;
  max-width: 780px;
  margin: 0 auto;
}

h1 {
  margin: 0 0 1rem;
}


.main {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: stretch;
      align-items: stretch;
  width: 100%;
}
label {
  font-size: 1.5rem;
}
input {
  margin-bottom: 1rem;
  font-size: 1.5rem;
  background-color: white;
  width: 100%;
}
label {
  -ms-flex-item-align: start;
      align-self: flex-start;
}


table {
  width: 100%;
  margin-bottom: 1rem;
}
tr {
  cursor: pointer;
}
th {
  text-align: left;
}
th:last-child{
  text-align: right;
}
td {
  text-align: left;
}
td:last-child{
  text-align: right;
}


.button-row {
  margin-top: 1rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

button {
  border: 1px solid black;
  background-color: white;
  cursor: pointer;
  min-width: 4.5em;
  margin-bottom: 1rem;
  padding: 0.5rem;
  font-size: 1.5rem;
}
button[disabled] {
  cursor: default;
  border-color: #CCC;
}

.average {
  width: 100%;
  font-weight: bold;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin-bottom: 1rem; 
}



